<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者:{{blog.author}}</p>
        <p>分类:</p>
        <ul><li v-for="f in blog.categories">{{f}}</li></ul>
    </div>
</template>

<script>
    export default {
        name: "single-blog",
        data() {
            return {
                id: this.$route.params.id,
                blog: {}
            }
        },
        created() {
            this.$http.get("https://vuedemo-b9605.firebaseio.com/posts/" + this.id+'.json').then(function(data) {
                // this.blog = data.body
                return data.json()
            }).then(function(data){
                this.blog=data
            })
        }
    }
</script>
<style scoped> 
#single-blog{
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background: #eee;
    border:1px dotted #aaa;
    box-sizing: border-box;
}

</style>




