<template>
    <div id="show-blogs">
        <h1>博客总览</h1>
        <input type="text" v-model="search" placeholder="搜索">
        <div v-for="blog in sousuo" class="single-blog">
            <router-link :to="'/blog/'+blog.id">
                <h3 v-yxh>{{blog.title}}</h3>
            </router-link>
            <article>
                {{blog.content | snoppet}}
            </article>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'show-blogs',
        data() {
            return {
                blogs: [],
                search: ""
            }
        },
        methods: {},
        components: {},
        computed: {
            sousuo: function() {
                return this.blogs.filter((blog) => {
                    return blog.title.match(this.search)
                })
            }
        },
        created() {
            this.$http.get("https://vuedemo-b9605.firebaseio.com/posts.json").then(function(data) {
                // this.blogs = data.body.slice(0, 10)
                return data.json()
            }).then(function(data){
                var blogsA=[]
                for(let key in data){
                    data[key].id=key
                    blogsA.push(data[key])
                }
                this.blogs=blogsA
            })
        }
    }
</script>

<style scoped>
    #show-blogs {
        max-width: 600px;
        margin: 0 auto;
        padding: 10px;
    }
    .single-blog {
        padding: 20px;
        margin: 20px 0;
        box-sizing: border-box;
        background: #eee
    }
    a {
        text-decoration: none
    }
    input[type="text"]{
        padding: 8px;
        width: 100%;
        box-sizing: border-box;
        outline:none
    }
</style>



