<template>
    <div id="add-blog">
        <h2>添加博客</h2>
        <form v-if="!submmited">
            <label>博客标题</label>
            <input type="text" v-model="blog.title" required>
            <label>博客内容</label>
            <textarea v-model="blog.content"></textarea>
            <div id="checkboxes">
                <label for="Vue.js">Vue.js</label>
                <input type="checkbox" value="Vue.js" v-model="blog.categories">
                <label for="Node.js">Node.js</label>
                <input type="checkbox" value="Node.js" v-model="blog.categories">
                <label for="React.js">React.js</label>
                <input type="checkbox" value="React.js" v-model="blog.categories">
                <label for="Angular4.js">Angular4.js</label>
                <input type="checkbox" value="Angular4.js" v-model="blog.categories">
            </div>
            <label>作者:</label>
            <select v-model="blog.author">
                  <option v-for="author in authors">
                      {{author}}
                  </option>
              </select>
            <button @click.prevent="post">添加博客</button>
        </form>
        <div v-if="submmited">
            <h3>发布成功</h3>
        </div>
        <div id="preview">
            <h3>博客总揽</h3>
            <p>博客标题:{{blog.title}}</p>
            <p>博客内容:</p>
            <p>{{blog.content}}</p>
            <p>博客分类</p>
            <ul>
                <li v-for="u in blog.categories">
                    {{u}}
                </li>
            </ul>
            <p>作者:{{blog.author}}</p>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'add-blog',
        data() {
            return {
                blog: {
                    title: "",
                    content: "",
                    categories: [],
                    author: "",
                },
                authors: ["李翔", "小慧慧", "大回", "吴志超", "袁志成"],
                submmited: false
            }
        },
        methods: {
            post() {
                this.$http.post("https://vuedemo-b9605.firebaseio.com/posts.json", this.blog).then(function(data) {
                    console.log(data)
                    this.submmited = true;
                })
            }
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #add-blog * {
        box-sizing: border-box;
    }
    #add-blog {
        margin: 20px auto;
        max-width: 600px;
        padding: 20px;
    }
    label {
        display: block;
        margin: 20px 0 10px
    }
    textarea{
        height: 200px;
    }
    input[type="text"],
    textarea,
    select {
        display: block;
        width: 100%;
        padding: 8px;
        resize: none
    }
    #checkboxes label {
        display: inline-block;
        margin-top: 0px
    }
    #checkboxes input {
        display: inline-block;
        margin-right: 10px
    }
    button {
        display: block;
        margin: 20px 0;
        background: crimson;
        color: #fff;
        border: 0;
        padding: 14px;
        border-radius: 4px;
        font-size: 18px;
        cursor: pointer;
    }
    #preview {
        padding: 10px 20px;
        border: 1px dotted #ccc;
        margin: 30px 0;
    }
    #preview h3 {
        margin-top: 10px;
    }
</style>



